___CSS_LOADER_EXPORT___.push([module.id, `

  .container {
    max-width: 800px;
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    gap: 20px; /* Gap between child elements */
  }

  h2 {
    color: #333;
    font-size: 24px;
    margin-bottom: 10px;
    text-align: center;
  }

  .restaurant-info {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .restaurant-info p {
    margin: 0; /* Remove default margin */
  }

  .restaurant-info ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  .restaurant-info ul li {
    margin-bottom: 5px;
  }

  .restaurant-info h3 {
    font-size: 18px;
    margin: 10px 0 5px 0; /* Top and bottom margin */
    color: #333;
  }

  img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }

  .button-container {
    display: flex;
    justify-content: center;
  }

  .restaurant-info button {
    background-color: #3E4EB8;
    color: #fff;
    border: none;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    outline: none; /* Remove outline on focus */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }

  .restaurant-info button:hover {
    background-color: #2E3C84;
  }

  .restaurant-info button:focus {
    background-color: #2E3C84;
    box-shadow: 0 0 0 3px rgba(62, 78, 184, 0.4); /* Example of focus state */
  }
  button {
    background-color: red; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }`, "",{"version":3,"sources":["webpack://./src/styles/detail.css"],"names":[],"mappings":";;EAEE;IACE,gBAAgB;IAChB,sBAAsB;IACtB,aAAa;IACb,uCAAuC;IACvC,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS,EAAE,+BAA+B;EAC5C;;EAEA;IACE,WAAW;IACX,eAAe;IACf,mBAAmB;IACnB,kBAAkB;EACpB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;EACX;;EAEA;IACE,SAAS,EAAE,0BAA0B;EACvC;;EAEA;IACE,qBAAqB;IACrB,UAAU;IACV,SAAS;EACX;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,oBAAoB,EAAE,0BAA0B;IAChD,WAAW;EACb;;EAEA;IACE,eAAe;IACf,YAAY;IACZ,kBAAkB;IAClB,sCAAsC;EACxC;;EAEA;IACE,aAAa;IACb,uBAAuB;EACzB;;EAEA;IACE,yBAAyB;IACzB,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,kBAAkB;IAClB,sCAAsC;IACtC,aAAa,EAAE,4BAA4B;IAC3C,wCAAwC;EAC1C;;EAEA;IACE,yBAAyB;EAC3B;;EAEA;IACE,yBAAyB;IACzB,4CAA4C,EAAE,2BAA2B;EAC3E;EACA;IACE,qBAAqB,EAAE,UAAU;IACjC,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,qBAAqB;IACrB,eAAe;EACjB","sourcesContent":["\n\n  .container {\n    max-width: 800px;\n    background-color: #fff;\n    padding: 20px;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n    border-radius: 8px;\n    display: flex;\n    flex-direction: column;\n    gap: 20px; /* Gap between child elements */\n  }\n\n  h2 {\n    color: #333;\n    font-size: 24px;\n    margin-bottom: 10px;\n    text-align: center;\n  }\n\n  .restaurant-info {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n  }\n\n  .restaurant-info p {\n    margin: 0; /* Remove default margin */\n  }\n\n  .restaurant-info ul {\n    list-style-type: none;\n    padding: 0;\n    margin: 0;\n  }\n\n  .restaurant-info ul li {\n    margin-bottom: 5px;\n  }\n\n  .restaurant-info h3 {\n    font-size: 18px;\n    margin: 10px 0 5px 0; /* Top and bottom margin */\n    color: #333;\n  }\n\n  img {\n    max-width: 100%;\n    height: auto;\n    border-radius: 8px;\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);\n  }\n\n  .button-container {\n    display: flex;\n    justify-content: center;\n  }\n\n  .restaurant-info button {\n    background-color: #3E4EB8;